"use client"

import { useState, useEffect } from "react"
import { useSession } from "next-auth/react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import Navbar from "@/components/navbar"
import CycleSelector from "@/components/cycle-selector"
import AdminSidebar from "@/components/admin-sidebar"

interface Cycle {
  id: string
  name: string
  month: number
  year: number
  isActive: boolean
}

export default function AdminDashboard() {
  const { data: session } = useSession()
  const [cycles, setCycles] = useState<Cycle[]>([])
  const [activeCycle, setActiveCycle] = useState<Cycle | null>(null)
  const [showNewCycle, setShowNewCycle] = useState(false)
  const [newCycleData, setNewCycleData] = useState({
    name: "",
    month: new Date().getMonth() + 1,
    year: new Date().getFullYear(),
  })
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    fetchCycles()
  }, [])

  const fetchCycles = async () => {
    try {
      const res = await fetch("/api/cycles")
      const data = await res.json()
      setCycles(data)
      if (data.length > 0) {
        setActiveCycle(data[0])
      }
    } catch (error) {
      console.error("Failed to fetch cycles:", error)
    } finally {
      setLoading(false)
    }
  }

  const handleCreateCycle = async () => {
    if (!newCycleData.name) {
      alert("Please enter a cycle name")
      return
    }

    try {
      const startDate = new Date(newCycleData.year, newCycleData.month - 1, 1)
      const endDate = new Date(newCycleData.year, newCycleData.month, 0)

      const res = await fetch("/api/cycles", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          ...newCycleData,
          startDate,
          endDate,
        }),
      })

      if (res.ok) {
        const newCycle = await res.json()
        setCycles([newCycle, ...cycles])
        setActiveCycle(newCycle)
        setShowNewCycle(false)
        setNewCycleData({
          name: "",
          month: new Date().getMonth() + 1,
          year: new Date().getFullYear(),
        })
      }
    } catch (error) {
      console.error("Failed to create cycle:", error)
    }
  }

  if (loading) {
    return <div className="flex items-center justify-center min-h-screen">Loading...</div>
  }

  return (
    <div className="flex h-screen bg-gray-50">
      <AdminSidebar />
      <div className="flex-1 flex flex-col">
        <Navbar />
        <div className="flex-1 overflow-auto p-8">
          <div className="max-w-6xl mx-auto space-y-6">
            <div className="flex justify-between items-center">
              <div>
                <h1 className="text-3xl font-bold text-gray-900">Admin Dashboard</h1>
                <p className="text-gray-600 mt-1">Welcome back, {session?.user?.name}</p>
              </div>
              <Button
                className="bg-blue-600 hover:bg-blue-700"
                onClick={() => setShowNewCycle(true)}
              >
                Create New Cycle
              </Button>
            </div>

            {showNewCycle && (
              <Card className="border-2 border-blue-200 bg-blue-50">
                <CardHeader>
                  <CardTitle>Create New Cycle</CardTitle>
                  <CardDescription>Start a new monthly performance cycle</CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="grid grid-cols-3 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Cycle Name</label>
                      <Input
                        placeholder="e.g., January 2025"
                        value={newCycleData.name}
                        onChange={(e) => setNewCycleData({ ...newCycleData, name: e.target.value })}
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Month</label>
                      <select
                        className="w-full px-3 py-2 border border-gray-300 rounded-md"
                        value={newCycleData.month}
                        onChange={(e) => setNewCycleData({ ...newCycleData, month: parseInt(e.target.value) })}
                      >
                        {Array.from({ length: 12 }, (_, i) => i + 1).map((month) => (
                          <option key={month} value={month}>
                            {new Date(2025, month - 1).toLocaleString("default", { month: "long" })}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-1">Year</label>
                      <select
                        className="w-full px-3 py-2 border border-gray-300 rounded-md"
                        value={newCycleData.year}
                        onChange={(e) => setNewCycleData({ ...newCycleData, year: parseInt(e.target.value) })}
                      >
                        {[2024, 2025, 2026].map((year) => (
                          <option key={year} value={year}>
                            {year}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="flex gap-2">
                    <Button onClick={handleCreateCycle} className="bg-green-600 hover:bg-green-700">
                      Create
                    </Button>
                    <Button onClick={() => setShowNewCycle(false)} variant="outline">
                      Cancel
                    </Button>
                  </div>
                </CardContent>
              </Card>
            )}

            <CycleSelector cycles={cycles} activeCycle={activeCycle} onSelect={setActiveCycle} />

            {activeCycle && (
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <Card>
                  <CardHeader>
                    <CardTitle>Quick Stats</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="flex justify-between items-center p-3 bg-blue-50 rounded">
                      <span className="text-gray-700">Active Cycle</span>
                      <span className="font-bold text-blue-600">{activeCycle.name}</span>
                    </div>
                    <div className="flex justify-between items-center p-3 bg-green-50 rounded">
                      <span className="text-gray-700">Team Members</span>
                      <span className="font-bold text-green-600">3 Agents</span>
                    </div>
                  </CardContent>
                </Card>

                <Card>
                  <CardHeader>
                    <CardTitle>Navigation</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-3">
                    <Button className="w-full justify-start bg-purple-600 hover:bg-purple-700" asChild>
                      <a href="/admin/kpi-calculator">KPI Calculator</a>
                    </Button>
                    <Button className="w-full justify-start bg-slate-600 hover:bg-slate-700" asChild>
                      <a href="/admin/daily-inputs">Daily Inputs</a>
                    </Button>
                  </CardContent>
                </Card>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
